
@-webkit-keyframes spin {
50% {
    -webkit-transform: rotate(100deg) scale(1.1);
    transform: rotate(100deg) scale(1.1);
}
100% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
}
}
@keyframes spin {
50% {
    -webkit-transform: rotate(100deg) scale(1.1);
    transform: rotate(100deg) scale(1.1);
}
100% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
}
}
@-webkit-keyframes spin-backwards {
50% {
    -webkit-transform: rotate(-45deg) scale(1.2);
    transform: rotate(-45deg) scale(1.2);
}
100% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
}
}
@keyframes spin-backwards {
50% {
    -webkit-transform: rotate(-45deg) scale(1.2);
    transform: rotate(-45deg) scale(1.2);
}
100% {
    -webkit-transform: rotate(0deg) scale(1);
    transform: rotate(0deg) scale(1);
}
}
@-webkit-keyframes bgchange {
50% {
    background: #111010;
}
}
@keyframes bgchange {
50% {
    background: #111010;
}
}
@-webkit-keyframes innershadow {
0% {
    box-shadow: 0 0 400px rgba(244, 66, 128, 0.15) inset;
}
50% {
    box-shadow: 0 0 400px rgba(210, 224, 235, 0.15) inset;
}
100% {
    box-shadow: 0 0 400px rgba(244, 66, 128, 0.15) inset;
}
}
@keyframes innershadow {
0% {
    box-shadow: 0 0 400px rgba(244, 66, 128, 0.15) inset;
}
50% {
    box-shadow: 0 0 400px rgba(210, 224, 235, 0.15) inset;
}
100% {
    box-shadow: 0 0 400px rgba(244, 66, 128, 0.15) inset;
}
}
@-webkit-keyframes innerpath {
0% {
    fill: #f44280;
}
50% {
    fill: #d2e0eb;
}
}
@keyframes innerpath {
0% {
    fill: #f44280;
}
50% {
    fill: #d2e0eb;
}
}
html, body {
  height: 100%;
  margin: 0;
  overflow: hidden;
}
body {
  background-color: black;
  -webkit-animation: bgchange 10s linear infinite;
          animation: bgchange 10s linear infinite;
}
#app {
  height: 100%;
}



